import React, { Component } from "react";
import axios from "axios";

class Create extends Component {
  constructor() {
    super();
    this.state = {
      name: "",
      title: "",
      subTitle: "",
      body: ""
    };
  }

  onChange = e => {
    const state = this.state;
    state[e.target.name] = e.target.value;
    this.setState(state);
  };

  onSubmit = e => {
    e.preventDefault();

    const { name, title, subTitle, body } = this.state;

    axios.post("/posts", { name, title, subTitle, body }).then(res => {
      this.props.history.push("/post");
    });
  };

  render() {
    const { name, title, subTitle, body } = this.state;

    return (
      <div className="container pb-5">
        <h1 className="display-4 pt-5 pb-4">Create a new post</h1>

        <form onSubmit={this.onSubmit} className="w-full max-w-md">
          <div className="flex items-center border-b border-b-2 border-teal py-2 mb-4">
            <input
              className="appearance-none bg-transparent border-none w-full text-grey-darker mr-3 py-1 px-2 leading-tight focus:outline-none"
              type="text"
              placeholder="Enter author alias"
              name="name"
              value={name}
              onChange={this.onChange}
            />
          </div>

          <div className="flex flex-wrap -mx-3 mb-6">
            <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
              <label
                className="block uppercase tracking-wide text-grey-darker text-xs font-bold mb-2"
                htmlFor="grid-title"
              >
                Article Title
              </label>
              <input
                className="appearance-none block w-full bg-grey-lighter text-grey-darker border border-red rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                id="grid-title"
                type="text"
                placeholder="Enter title here ..."
                name="title"
                value={title}
                onChange={this.onChange}
              />
            </div>
            <div className="w-full md:w-1/2 px-3">
              <label
                className="block uppercase tracking-wide text-grey-darker text-xs font-bold mb-2"
                htmlFor="grid-subTitle"
              >
                Article Caption
              </label>
              <input
                className="appearance-none block w-full bg-grey-lighter text-grey-darker border border-grey-lighter rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-grey"
                id="grid-subTitle"
                type="text"
                placeholder="Enter sub-title ..."
                name="subTitle"
                value={subTitle}
                onChange={this.onChange}
              />
            </div>
          </div>
          <div className="flex flex-wrap -mx-3 mb-6">
            <div className="w-full px-3">
              <label
                className="block uppercase tracking-wide text-grey-darker text-xs font-bold mb-2"
                htmlFor="grid-body"
              >
                Article Body
              </label>
              <textarea
                className="appearance-none block w-full bg-grey-lighter text-grey-darker border border-grey-lighter rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-grey"
                id="grid-body"
                type="text"
                placeholder="Enter article body ..."
                rows="10"
                name="body"
                value={body}
                onChange={this.onChange}
              />
            </div>
          </div>
          {/*           <div className="flex flex-wrap -mx-3 mb-2">
            <div className="w-full md:w-1/3 px-3 mb-6 md:mb-0">
              <label
                className="block uppercase tracking-wide text-grey-darker text-xs font-bold mb-2"
                htmlFor="grid-state"
              >
                Status
              </label>
              <div className="relative">
                <select
                  className="block appearance-none w-full bg-grey-lighter border border-grey-lighter text-grey-darker py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-grey"
                  id="grid-state"
                >
                  <option>Public</option>
                  <option>Private</option>
                  <option>Unlisted</option>
                </select>
                <div className="pointer-events-none absolute pin-y pin-r flex items-center px-2 text-grey-darker">
                  <svg
                    className="fill-current h-4 w-4"
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 20 20"
                  >
                    <path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" />
                  </svg>
                </div>
              </div>
            </div>
          </div> */}
          <button
            type="submit"
            className="btn btn-outline-primary btn-block btn-lg mt-4"
          >
            Create Post
          </button>
        </form>
      </div>
    );
  }
}

export default Create;
