import React, { Component } from "react";
import axios from "axios";
import { Link } from "react-router-dom";

class Posts extends Component {
  constructor(props) {
    super(props);
    this.state = {
      posts: []
    };
  }

  componentDidMount() {
    axios.get("/posts").then(res => {
      this.setState({ posts: res.data });
      console.log(this.state.posts);
    });
  }

  render() {
    return (
      <div>
        <div className="container" style={{ marginBottom: "575px" }}>
          <h1 className="display-4 pt-5 pb-3">View all posts</h1>
          {this.state.posts.map(p => (
            <span className="border border-white">
              {/*               <img
                src={p.image}
                class="card-img-top"
                alt="No image available"
              /> */}
              <div className="card mb-3">
                <div className="card-body">
                  <Link to={`/show/${p.id}`}> {p.title}</Link>

                  <p className="card-text">{p.subTitle}</p>
                  <p className="card-text">
                    <small className="text-muted">Created by: {p.name}</small>
                  </p>
                </div>
              </div>
            </span>
          ))}
        </div>
      </div>
    );
  }
}

export default Posts;
